.sequenceStep{
  margin-left: 50px;
  width: 100%;  
  display: block;  
}
.step {
  height: 8px;
  /*width =  100/16 */
  width: 5.95%;
  display: -ms-inline-flexbox;
  display: inline-flex;
}

.active {
  -webkit-transition: all 100ms 0s ease;
  -o-transition: all 100ms 0s ease;
  transition: all 100ms 0s ease;
  background-color: #3f51b5;
  color: #fff;
}



